// typograhy 
$font-family: 'Raleway',
  sans-serif;
$font-size: 14px;
$font-weight-four: 400;
$font_weight-seven: 700;

// colors 
$pale_blue: hsl(243, 100%, 93%);
$grayish_blue: hsl(229, 7%, 55%);
$dark_blue: hsl(228, 56%, 26%);
$very_dark_blue: hsl(229, 57%, 11%);

// media query for laptop and desktop 
@mixin desktop {
  @media (min-width: 64.0625em) and (max-width:100em) {
    @content;
  }
}